










import Vue from 'vue'
import Page from '@/components/Page.vue'

export default Vue.extend({
  components: { Page },
  methods: {
    showToptips(value: boolean): void {
      if (value) {
        this.$toptips.show('错误提示', { duration: 0 })
      } else {
        this.$toptips.hide()
      }
    },
  },
})
